import { useQuery } from "react-query";
import { useDispatch, useSelector } from "react-redux";
import { config } from "../../config/api";
import { updateUserInfo } from "../../redux/action/userAction";
import useAxios from "./useAxios";
import useHandleErrors from "./useHandleErrors";

export const useUserInfo = (props) => {
  const { onSuccess } = props || {};
  const auth = useSelector((state) => state?.userStatus?.auth);
  const dispatch = useDispatch();

  const [handleError] = useHandleErrors();
  const Axios = useAxios();
  const queryInstance = useQuery(
    ["user", "userInfo"],
    async () => {
      const { data } = await Axios.get(`${config.apiGateWay}/dashboard/user`);
      // console.log(data);
      return data.user;
    },
    {
      onError: (err) => {
        handleError(err);
      },
      onSuccess: (data) => {
        dispatch(updateUserInfo({ user: data }));
        onSuccess?.(data);
      },
      enabled: auth,
    }
  );
  return queryInstance;
};
