import { m } from "framer-motion";
import React from "react";
import styled from "styled-components";
import { roleVariant } from "./variant";

function Role({ className, color, show }) {
  // function comp(color, txt) {
  //   return (
  //     <div
  //       style={{
  //         border: "2px solid " + color,
  //         borderRadius: "50%",
  //         width: 24,
  //         height: 24,
  //         color: color,
  //       }}
  //     >
  //       <i className="fas fa-crown"></i>
  //     </div>
  //   );
  // }
  return (
    <m.div
      className={`${className} ${show ? "" : "d-none"}`}
      variants={roleVariant}
      transition={{
        type: "spring",
        duration: 0.3,
        mass: 1,
        stiffness: 130,
      }}
    >
      
    </m.div>
  );
}

export default styled(Role)`
  position: absolute;
  font-size: 12px;
  top: 0;
  left: 50%;
  width: 24px;
  height: 24px;
  transform: translate(-50%, -60%);
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  -webkit-font-smoothing: antialiased;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  color: ${(props) => props.theme.colorSecondary || "yellow"};
  border-radius: 50%;
  background-color: #fafafa;
  display: flex;
  justify-content: center;
  align-items: center;
`;
