import { useSelector } from "react-redux";

const useNav = (props) => {
  const user = useSelector((state) => state.userStatus?.userInfo?.user);
  const auth = useSelector((state) => state.userStatus?.auth);
  const listNav = [
    [
      { name: "Trang chủ", link: "/" },
      { name: "Chính sách", link: "/agency-policy" },
      // { name: "Kích hoạt báo cáo", link: "/getting-report" },
      // { name: "Về chúng tôi", link: "/" },
      { name: "Bài viết", link: "/blogs" },
    ],
    [
      { name: "Trang chủ", link: "/" },
      { name: "Chính sách", link: "/agency-policy" },
      // { name: "Kích hoạt báo cáo", link: "/getting-report" },
      { name: "Bài viết", link: "/blogs" },
      { name: "Quản lý", link: "/agency-dashboard" },
    ],
    [
      { name: "Trang chủ", link: "/" },
      { name: "Chính sách", link: "/agency-policy" },
      // { name: "Kích hoạt báo cáo", link: "/getting-report" },
      // { name: "Về chúng tôi", link: "/" },
      { name: "Bài viết", link: "/blogs" },
    ],
  ];
  const defautNav = [
    { name: "Trang chủ", link: "/" },
    { name: "Chính sách", link: "/agency-policy" },
    // { name: "Về chúng tôi", link: "/" },
    { name: "Bài viết", link: "/blogs" },
  ];
  return (listNav[user?.role] || defautNav).concat(
    auth ? (!user?.is_active ? [{ name: "Kích hoạt báo cáo", link: "/getting-report" }] : []) : []
  );
};

export default useNav;
