import { useMutation, useQueryClient } from "react-query";
import useAxios from "./useAxios";
import { config } from "../../config/api";
import useHandleErrors from "./useHandleErrors";
import { toast } from "react-toastify";
import { useRef } from "react";
import { shallowEqual, useDispatch, useSelector } from "react-redux";

const usePdfExport = () => {
  const dispatch = useDispatch();
  const axios = useAxios();
  const [handleError] = useHandleErrors();
  const toastId = useRef(null);
  const queryClient = useQueryClient();
  const loading = useSelector(
    (state) => !!state?.loading?.export_pdf,
    shallowEqual
  );

  // const isMutatingPosts = useIsMutating(['posts'])
  // const queryClient = useQueryClient();
  const query = useMutation(
    async () => {
      const { data } = await axios.post(
        `${config.apiGateWay}/dashboard/user/export-pdf`,
        null,
        {
          timeout: 100000,
          timeoutErrorMessage: "Quá lâu",
        }
      );
      console.log(data);
      return data?.data;
    },
    {
      mutationKey: ["user", "useExportPdf"],
      onMutate: () => {
        dispatch({ type: "EXPORT_PDF_REQUEST" });
        toastId.current = toast("File PDF của bạn đang được xuất", {
          autoClose: false,
        });
      },
      onError: (err) => {
        dispatch({ type: "EXPORT_PDF_ERROR", payload: err?.response?.data });
        if (toast.isActive(toastId.current)) {
          toast.update(toastId.current, {
            render: () => <span>{err?.response?.data?.message}</span>,
            type: toast.TYPE.ERROR,
            position: toast.POSITION.TOP_RIGHT,
          });
        } else {
          toastId.current = toast.success(
            () => <span>err?.response?.data?.message</span>,
            {
              autoClose: false,
              position: toast.POSITION.TOP_RIGHT,
            }
          );
        }
        handleError(err);
      },
      onSuccess: (data) => {
        if (toast.isActive(toastId.current)) {
          toast.update(toastId.current, {
            render: () => (
              <span>
                Pdf của bạn đã được xuất{" "}
                <a
                  target="_blank"
                  href={`${config.apiResource}${data?.link_p}`}
                  rel="noreferrer"
                >
                  xem ngay !
                </a>
              </span>
            ),
            type: toast.TYPE.SUCCESS,
            position: toast.POSITION.TOP_RIGHT,
          });
        } else {
          toastId.current = toast.success(
            () => (
              <span>
                Pdf của bạn đã được xuất{" "}
                <a
                  target="_blank"
                  href={`${config.apiResource}${data?.link_p}`}
                  rel="noreferrer"
                >
                  xem ngay !
                </a>
              </span>
            ),
            {
              autoClose: false,
              position: toast.POSITION.TOP_RIGHT,
            }
          );
        }
        dispatch({ type: "EXPORT_PDF_SUCCESS" });
      },
      onSettled: () => {
        queryClient.invalidateQueries(["user", "pdfs"]);
      },
    }
  );
  return { ...query, isLoading: loading };
};

export default usePdfExport;
