import React, { memo, useEffect, useState } from "react";
import { Modal } from "react-bootstrap";
import ReactPlayer from "react-player/lazy";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { config } from "../../../config/api";
import { useHome } from "../../hooks/useHome";

const EventModal = ({ className }) => {
  const [show, setShow] = useState(false);
  const { data, isLoading } = useHome();
  const { popup } = data || {};
  useEffect(() => {
    if (isLoading) return;
    if (popup?.type === 1) return setShow(true);
    const imgaes = new Image();
    // imgaes.src = "https://www.w3schools.com/w3css/img_lights.jpg";
    imgaes.src = `${config.apiResource}${popup?.media}`;
    imgaes.onload = () => {
      setShow(true);
    };
    return () => {};
  }, [popup, isLoading]);
  return (
    <Modal
      show={!isLoading && show}
      className={className}
      contentClassName="modal__custom p-0"
      dialogClassName="modal__custom__dialog container"
      centered
    >
      <div className="modal-header modal__custom__header">
        <button
          type="button"
          className="btn-close modal__custom__button--close text-white"
          data-bs-dismiss="modal"
          aria-label="Close"
          onClick={() => setShow?.(false)}
          style={{ filter: "invert(1)" }}
        ></button>
      </div>
      <div className="modal-body modal__custom__body p-0">
        {popup?.type === 0 ? (
          <img
            src={`${config.apiResource}${popup?.media}`}
            alt=""
            className="mb-3"
            onLoad={() => setShow(true)}
          />
        ) : (
          <div className="step__form__video">
            <ReactPlayer
              width="100%"
              height="auto"
              url={`https://www.youtube.com/embed/${popup?.media}`}
              muted={true}
              playing={true}
              style={{ width: "100%", height: "100%" }}
            />
          </div>
        )}

        <div className="row px-3">
          <div className="col-12 col-lg-3 align-items-start d-flex">
            <span className="fs__title fw-bold p-1">{popup?.name}</span>
          </div>
          <div className="col-12 col-lg-9">
            <p
              className="fw-bolder fs__text"
              style={{
                borderColor: "#ffc367 !important",
                borderWidth: "2px !important",
              }}
            >
              Lưu ý:
            </p>
            <p className="fs__text">{popup?.desc}</p>
          </div>
        </div>
      </div>
      <div className="modal-footer modal__custom__footer">
        <button
          type="button"
          className="btn button-secondary--outline"
          data-bs-dismiss="modal"
          onClick={() => setShow?.(false)}
        >
          Đóng
        </button>
        <button
          type="button"
          className="btn button-secondary"
          onClick={() => setShow?.(false)}
        >
          <Link to={popup?.link}>Đến ngay</Link>
        </button>
      </div>
    </Modal>
    // <div
    //   className={`${className} modal show`}
    //   id="staticBackdrop"
    //   data-bs-backdrop="static"
    //   data-bs-keyboard="false"
    //   tabIndex="-1"
    //   aria-labelledby="staticBackdropLabel"
    //   aria-hidden="true"
    // >

    // {/* </div> */}
  );
};

export default memo(styled(EventModal)`
  .modal__custom {
    position: relative;
    background-color: #fafafa;
    color: black;
    &__dialog {
      @media (min-width: 576px) {
        max-width: 540px;
      }
      @media (min-width: 768px) {
        max-width: 720px;
      }
      @media (min-width: 992px) {
        max-width: 960px;
      }
      @media (min-width: 1200px) {
        max-width: 1140px;
      }
      @media (min-width: 1400px) {
        max-width: 1320px;
      }

      max-width: fit-content;
    }
    &__body {
      img {
        border: none;
        width: 100%;
        max-height: 350px;
        object-fit: cover;
      }
    }
    &__header {
      border: none;
      position: absolute;
      right: 8px;
      top: 8px;
      padding: 0;
    }
    &__footer {
      border: 0;
      button {
        border-radius: 0;
      }
    }
    &__button--close {
      z-index: 2;
      margin: 0;
      color: #fafafa !important;
      font-weight: bolder;
      &:hover {
        color: ${(props) => props.theme.colorTextTitle};
      }
    }
  }
`);
