import { m } from "framer-motion";
import React from "react";

import { Link } from "react-router-dom";
import styled from "styled-components";

import useLogoutAction from "../../hooks/useLogoutAction";
import { useUserInfo } from "../../hooks/useUserInfo";
import Role from "./Role";
import { avaVariant } from "./variant";

const UserInfo = ({ fullname, coin, className }) => {
  // console.log("userInfo rerender");
  const { data: user } = useUserInfo();
  const logout = useLogoutAction();
  // const user = useSelector((state) => state.userStatus?.userInfo?.user);
  // const { avaOpacity, avaScale, roleOpacity, roleScale } = useSpring({
  //   to: [
  //     { avaScale: 1, avaOpacity: 1 },
  //     { roleScale: 1, roleOpacity: 1 },
  //   ],
  //   from: { avaScale: 0, avaOpacity: 0, roleScale: 0, roleOpacity: 0 },
  //   config: config.wobbly,
  //   delay: 1600,
  // });
  return (
    <div
      className={`${className} header__user d-none d-xl-flex align-items-center`}
    >
      <Link to="/user" className="d-flex align-items-center">
        <span className="text-capitalize">
          {fullname?.split(" ").slice(-1)}
        </span>
        <m.div
          style={{ position: "relative" }}
          transition={{ when: "beforeChildren", staggerChildren: 0.3 }}
        >
          <m.img
            variants={avaVariant}
            transition={{
              type: "spring",
              duration: 0.3,
              mass: 1,
              stiffness: 130,
            }}
            src="https://www.sohoceravietnam.com/public/img/meow.jpg"
            alt="Ảnh đại diện"
          />
          <Role show={user?.role > 0} />
        </m.div>
      </Link>
      <span
        className="logout__btn ms-2 d-none d-lg-inline-flex"
        onClick={logout}
      >
        <i className="fas fa-sign-out-alt"></i>
      </span>
    </div>
  );
};

export default styled(UserInfo)`
  &.header {
    &__user {
      color: #fafafa;
      /* display: flex;
      flex-direction: row;
      vertical-align: center; */
      span {
        margin-right: 10px;
      }
      a {
        &:hover {
          color: #f3eaa7;
        }
        position: relative;
      }
      img {
        /* $size: 40px; */
        width: 40px;
        height: 40px;
        border-radius: 100%;
      }
    }
  }
  .logout__btn {
    align-self: center;
    background-color: #fafafa;
    color: black;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
`;
