import React from "react";

import { Modal } from "react-bootstrap";
import { useForm } from "react-hook-form";
import styled from "styled-components";
import useModal from "../../hooks/useModal";
import usePdfExport from "../../hooks/usePdfExport";
import FormErrors from "../FormErrors";
const PdfExportModal = ({ className }) => {
  const { isOpen, close } = useModal("pdf_export");
  const { mutate: exportPdf, isLoading, error } = usePdfExport();
  const { register, handleSubmit, errors } = useForm();
  const handleExport = async () => {
    if (isLoading) return;
    exportPdf();
    close();
  };
  return (
    <Modal
      show={isOpen}
      className={className}
      contentClassName="modal__custom p-0"
      dialogClassName="modal__custom__dialog container"
      centered
    >
      <form className="p-3" onSubmit={handleSubmit(handleExport)}>
        <div className="modal-header modal__custom__header">
          <button
            type="button"
            className="btn-close modal__custom__button--close text-white"
            data-bs-dismiss="modal"
            aria-label="Close"
            onClick={close}
            // style={{ filter: "invert(1)" }}
          ></button>
        </div>
        <div className="modal-body modal__custom__body p-3 d-flex align-items-center flex-column">
          <h1 className="fs__title fw-bolder d-block align-self-start mb-3">
            Nhập thông tin
          </h1>
          <div className="form__input d-flex flex-column w-100">
            <input
              type="text"
              name="name"
              placeholder="Tên Đầy Đủ"
              className="w-100"
              ref={register({
                required: "Bạn cần nhập tên",
                pattern: {
                  value: /^[^`~!@#$%^&*()_+={}\[\]|\\:;“’<,>.?๐฿]*$/,
                  message: "Tên không được chứa ký tự đặc biệt",
                },
              })}
            />
            <input
              type="date"
              name="birth"
              className="w-100"
              ref={register({ required: "Bạn cần nhập ngày tháng năm sinh" })}
            />
          </div>
          <FormErrors errors={errors} />
          {/* <div className="error mt-2">{error?.response?.data?.message}</div> */}

          {/* {popup?.type === 0 ? (
            <img
              src={`${config.apiResource}${popup?.media}`}
              alt=""
              className="mb-3"
              onLoad={() => setShow(true)}
            />
          ) : (
            <div className="step__form__video">
              <ReactPlayer
                width="100%"
                height="auto"
                url={`https://www.youtube.com/embed/${popup?.media}`}
                muted={true}
                playing={true}
                style={{ width: "100%", height: "100%" }}
              />
            </div>
          )} */}

          {/* <div className="row px-3">
          <div className="col-12 col-lg-3 align-items-start d-flex">
            <span className="fs__title fw-bold p-1">{popup?.name}</span>
          </div>
          <div className="col-12 col-lg-9">
            <p
              className="fw-bolder fs__text"
              style={{
                borderColor: "#ffc367 !important",
                borderWidth: "2px !important",
              }}
            >
              Lưu ý:
            </p>
            <p className="fs__text">{popup?.desc}</p>
          </div>
        </div> */}
        </div>
        <div className="modal-footer modal__custom__footer">
          <button
            type="button"
            className="btn button-secondary--outline"
            data-bs-dismiss="modal"
            onClick={close}
          >
            Đóng
          </button>
          <button
            type="submit"
            className="btn button-secondary"
            // onClick={() => setShow?.(false)}
          >
            Xuất file
          </button>
        </div>
      </form>
    </Modal>
  );
};

export default styled(PdfExportModal)`
  input {
    margin: 10px 0;
    text-indent: 20px;
    background-color: black;
    color: #fafafa;
    height: 50px;
    border-radius: 8px;
    border: none;
    font-weight: bold;
    outline: none;
    &:focus {
      border: 3px solid ${(props) => props.theme.colorSecondary};
    }
    &::-webkit-input-placeholder {
      color: #fafafa;
    }
  }
  input[type="date"] {
    text-indent: 10px;
    &::-webkit-calendar-picker-indicator {
      cursor: pointer;
      filter: invert(1);
      margin-right: 10px;
    }
  }
`;
