import React from "react";
import { useLocation } from "react-router";
import { Link } from "react-router-dom";
import styled from "styled-components";

export const Footer = ({ className }) => {
  const exception = ["/login", "/signup"];
  const location = useLocation();
  if (exception.includes(location.pathname)) return null;
  return (
    <div className={`${className} mt-5`}>
      <section className={`${className} footer container my-5 p-0`}>
        <div className="row py-5">
          <div className="col-8">
            <p className="fs__text fw-bold">CỘNG ĐỒNG PHÁT TIỂN BẢN THÂN ERA VIỆT NAM © 2020</p>
            <div className="d-flex fs__details flex-column flex-sm-row">
              <div className="col-12 col-sm-6">
                <p>Email: sohocera.vn@gmail.com</p>
                {/* <p>Cấp ngày: 16/07/2020.</p>
                <p>Nơi cấp: Sở Kế hoạch và Đầu tư TP.Hồ Chí Minh.</p> */}
              </div>
              <div className="col-12 col-sm-6">
                <p>
                  <span>
                    <i className="fas fa-map-marker-alt"></i>
                  </span>
                  &nbsp;Landmark 81 – 772 Điện Biên Phủ, Phường 1, Q. Bình Thạnh, TP.HCM.
                </p>
                <p>
                  <span>
                    <i className="fas fa-phone-alt"></i>
                  </span>
                  &nbsp;0982 123 507
                </p>
              </div>
            </div>
          </div>
          <div className="footer__link col-4">
            <p className="fs__text fw-bold">Liên kết</p>
            <nav>
              <ul className="list-unstyled">
                <li>
                  <Link to="/">Trang chủ</Link>
                </li>
                <li>
                  <Link to="/#faq">Về chúng tôi</Link>
                </li>
                <li>
                  <Link to="/post">Bài viết</Link>
                </li>
                {/* <li>
                  <a href="">FAQs</a>
                </li> */}
              </ul>
            </nav>
          </div>
        </div>
      </section>
    </div>
  );
};

export default styled(Footer)`
  background-color: ${(props) => props.theme.colorSecondary};
  color: #fafafa;
  & > .row {
    border-top: 2px solid;
    border-bottom: 2px solid;
    border-color: #ffc367;
    background-position: center center;
  }
  .footer__link a {
    color: #fafafa;
  }
`;
