import React from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import styled from "styled-components";
import useNav from "../../hooks/useNav";
import { useUserInfo } from "../../hooks/useUserInfo";

// const config = { mass: 5, tension: 2000, friction: 200 };

const MobileMenu = ({ className, isOpen, closeNav }) => {
  const { data: user } = useUserInfo();
  const auth = useSelector((state) => state.userStatus.auth);
  const userInfo = useSelector((state) => state.userStatus.userInfo);
  const nav = useNav();

  return (
    <section
      className={`${className} header navbar flex-column flex-lg-row p-lg-1 d-flex d-xl-none ${
        isOpen ? "show" : ""
      }`}
    >
      <div className="header__logo">
        <img src="https://www.sohoceravietnam.com/public/img/logo.png" alt="Era Logo" />
      </div>
      {auth && (
        <div className={`header__user`}>
          <Link to="/user" onClick={closeNav}>
            <img src="https://www.sohoceravietnam.com/public/img/meow.jpg" alt="Ảnh đại diện" />
            {/* <span>{user?.fullname?.split(" ").slice(-1)}</span> */}
          </Link>
        </div>
      )}
      {/* <div className="header__mobile-menu d-flex flex-column flex-lg-row justify-content-center justify-content-lg-between"> */}
      <div className="header__link">
        <nav className="d-flex flex-column flex-xl-row" style={{ columnGap: 4 }}>
          {nav?.map(({ name, link }) => (
            <Link key={name} to={link} onClick={closeNav}>
              {name}
            </Link>
          ))}
        </nav>
      </div>
      <div className="header__button">
        <nav className="d-flex" style={{ columnGap: 4 }}>
          {auth ? (
            <div className="button-secondary--outline fw-bold header__button__logout">
              Đăng xuất
            </div>
          ) : (
            <>
              <Link to="/login" className="button-secondary fw-bold fw-bold header__button__login">
                Đăng nhập
              </Link>
              <Link to="/signup" className="button-secondary fw-bold header__button__signup">
                Đăng ký
              </Link>
            </>
          )}
        </nav>
      </div>
      {/* </div> */}
    </section>
  );
};

export default styled(MobileMenu)`
  transform: translateX(100%);
  transition: transform 0.3s ease-in;
  padding-top: 66px;
  background-color: ${(props) => props.theme.colorSecondary};
  /* z-index: 999; */
  &.show {
    transform: translateX(0);
  }
  .header {
    &__user {
      color: #fafafa;
      font-weight: bold;
      font-size: 24px; /* display: flex;
      flex-direction: row;
      vertical-align: center; */
      span {
        margin-left: 10px;
        color: #fafafa;
      }
      img {
        /* $size: 40px; */
        width: 140px;
        height: 140px;
        border-radius: 100%;
      }
    }
    &__logo {
      z-index: 3;
      > img {
        width: 300px;
        height: 50px;
        object-fit: cover;
        border: none;
      }
    }
    &__link {
      height: 340px;
      a {
        font-weight: bold;
        color: white;
        margin: 0 10px;
        &:hover {
          font-weight: bolder;
          color: #ffd597;
        }
      }
    }
    &__button {
      &__logout {
        background-color: #fafafa;
      }
      &__login {
        background-color: #fafafa;
        color: ${(props) => props.theme.colorSecondary};
      }
      &__signup {
        &:hover {
          color: #fafafa;
        }
      }
      a {
        padding: 10px 20px;
      }
      div {
        padding: 10px 20px;
      }
    }
  }
  @media ${(props) => props.theme.breakPoints.xl} {
    z-index: 2;

    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    .header {
      &__mobile-menu {
        z-index: 2;

        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 20px;
        padding-top: 120px;
      }
      &__link {
        nav {
          row-gap: 16px;
        }
        a {
          font-weight: bold;
          color: white;
          margin: 0 10px;
          font-size: 24px;
        }
      }
    }
  }
`;
