import React, { useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import styled from "styled-components";
import MobileNav from "../MobileNav";
import Hamburger from "hamburger-react";
import { shallowEqual, useSelector } from "react-redux";
import UserInfo from "../UserInfoNav";
import useNav from "../../hooks/useNav";
import { m } from "framer-motion";
import { headerVariant, navVariant } from "./animationVariant";

const Header = ({ className }) => {
  // const exception = ["/login", "/signup"];
  const auth = useSelector((state) => state.userStatus.auth);
  const userInfo = useSelector(
    (state) => state.userStatus.userInfo,
    shallowEqual
  );

  const nav = useNav();
  const history = useHistory();
  useEffect(() => {
    const unlisten = history.listen(() => {
      setIsMobileNavOpen(false);
    });
    return unlisten;
  }, [history]);

  const [isMobileNavOpen, setIsMobileNavOpen] = useState(false);
  return (
    <>
      <m.section
        variants={headerVariant}
        key={userInfo?.user?.id}
        animate="show"
        initial="initial"
        transition={{
          delay: 0.5,
          duration: 0.5,
          type: "just",
          when: "beforeChildren",
          staggerChildren: 0.3,
        }}
        className={`${className} py-0 py-lg-1`}
        // style={headerAnimated}
      >
        <div className={` header navbar container`}>
          <Link to="/">
            <div
              className={`header__logo ${isMobileNavOpen ? "invisible" : ""}`}
            >
              <img
                src="https://www.sohoceravietnam.com/public/img/logo.png"
                alt="Era Logo"
              />
            </div>
          </Link>

          <div className="header__link d-none d-xl-block">
            <m.nav
              // initial="initial"
              // animate="show"
              transition={{ staggerChildren: 0.3, when: "beforeChildren" }}
              className="d-flex flex-column flex-lg-row"
              style={{ columnGap: 4 }}
            >
              {nav.map(({ name, link }) => {
                return (
                  <m.span
                    // animate="show"
                    className="text-upercase"
                    style={{ textTransform: "uppercase" }}
                    key={name}
                    variants={navVariant}
                    transition={{ duration: 0.5, type: "spring", bounce: 0.5 }}
                  >
                    <Link to={link}>{name}</Link>
                  </m.span>
                );
              })}
            </m.nav>
          </div>

          {auth ? (
            <UserInfo {...userInfo?.user} />
          ) : (
            <div className="header__button d-none d-xl-block">
              <nav className="d-flex" style={{ columnGap: 4 }}>
                <Link
                  to="/login"
                  className="button-secondary fw-bold fw-bold header__button__login"
                >
                  Đăng nhập
                </Link>
                <Link
                  to="/signup"
                  className="button-secondary fw-bold header__button__signup"
                >
                  Đăng ký
                </Link>
              </nav>
            </div>
          )}

          <div className="header__mobile-menu__btn d-block d-xl-none d-flex align-content-center">
            <Hamburger
              toggled={isMobileNavOpen}
              toggle={setIsMobileNavOpen}
              duration={0.3}
              color="#fafafa"
            />
          </div>
        </div>
      </m.section>
      <MobileNav
        isOpen={isMobileNavOpen}
        closeNav={() => setIsMobileNavOpen(false)}
      />
    </>
  );
};

export default styled(Header)`
  z-index: 99;
  position: sticky;
  top: 0;
  /* padding-top: 64px; */
  background-color: ${(props) => props.theme.colorSecondary};
  .header {
    &__mobile-menu {
      &__btn {
        z-index: 3;
      }
    }
    &__logo {
      div {
        height: 50px;
        background-image: url(/images/logo.png);
        width: auto;
      }
      > img {
        width: 300px;
        height: 50px;
        object-fit: cover;
        border: none;
      }
    }
    &__link {
      a {
        font-weight: bold;
        color: white;
        margin: 0 10px;
        &:hover {
          font-weight: bolder;
          color: #f3eaa7;
        }
      }
    }

    &__button {
      &__login {
        background-color: #fafafa;
        color: ${(props) => props.theme.colorSecondary};
      }
      &__signup {
        &:hover {
          color: #fafafa;
        }
      }
      a {
        padding: 10px 20px;
      }
    }
  }
`;
